.header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    background-color: black;
}

.link {
    text-decoration: none;
    color: white;
    transition: 0.5s all;
    margin-right: 10px;
}

.link:hover {
    color: orange;
}